@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
html,body {
  padding: 0;
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  min-height: 100vh;
  background-color: #ECF0F1;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,
*::after,
*::before{
  box-sizing: border-box;
}

.app{
  display: flex;
  height: 100vh;
}

h1, h2, h3, h4, h5, h6{
  margin-top: 0;
}

ul li{
  list-style: none;
}

input{
  background-color: transparent;
  border: none;
  padding: 0.75rem 0.4rem;
  width: 100%;
}
button{
  border: none;
}
input:focus,
button:focus{
  outline: none;
}
button:hover{
  cursor: pointer;
}

@media (max-width: 980px){
  .app{
    flex-direction: column;
    justify-content: center;
    height: 100%;
  }
}
.header{
    display: flex;
    align-items: center;
}
.app-logo img{
    width: 100%;
}
.vertical-seperator{
    width: 1px;
    height: 30px;
    background-color: #aaaaaa;
    margin: 0 1.5rem 0 1rem;
}
.app-header{
    font-weight: 200;
    margin: 0;
    color: #454545;
    font-size: 1.5rem;
}

@media (max-width: 765px){
    .app-header,
    .vertical-seperator{
        display: none;
    }
}

.cart__item{
    display: flex;
    align-items: center;
    padding: 1.2rem;
}
.cart__item svg{
    width: 25px;
}
.bg-gray{
    background-color: #E5E9EA;
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 1.5rem;
    width: 90%;
    flex: 0 0 90%;
}

.product__img{
    position: relative;
    overflow: hidden;
    border-radius: 50%;
    width: 80px;
    height: 80px;
}
.product__img img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.padding-l-2{
    padding-left: 2rem;
    margin: 0;
}
.product__name{
    margin: 0;
}
.product__ref{
    margin-bottom: 0;
    color: #707070;
}

.set-count__section{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.set-count__buttons{
    display: flex;
    flex-direction: column;
    margin: 0 0.7rem;
}
.btn-gray__rounded{
    background-color: #707070;
    color: #ffffff;
    border-radius: 50%;
    width: 25px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0.2rem;
}
.btn-gray__rounded svg{
    width: 20px;
}

.remove{
    width: 10%;
    flex: 0 0 10%;
    display: flex;
    justify-content: center;
    align-items: center;
}

@media (max-width: 765px){
    .cart__item{
        width: 100%;
        padding: 1rem 0;
    }
    .bg-gray{
        padding: 1rem 1.5rem 1rem 0.75rem;
    }
    .padding-l-2{
        padding-left: 1rem;
    }
    .product__img,
    .product__ref{
        display: none;
    }
    .product__color{
        font-size: 1rem;
    }
}
@media (max-width: 330px){
    .product__color{
        display: none;
    }
}
.footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem;
}
.footer__left{
    display: flex;
    align-items: center;
    cursor: pointer;
}
.footer__left svg{
    width: 22px;
    margin-right: 0.5rem;
}

.footer__left h4,
.footer__right h4{
    margin: 0;
}

@media (max-width: 765px){
    .footer{
        padding: 1.5rem 1rem 1rem ;
    }
}
@media (max-width: 330px){
    .footer{
        flex-direction: column-reverse;
        align-items: flex-start;
    }
    .footer__right h4{
        padding-bottom: 1rem;
    }
}
.cart__container{
    margin: 2rem 0;
}
.cart-section__container{
    flex: 0 0 70%;
    width: 70%;
    overflow-y: scroll;
    padding: 2rem 3rem;
}

@media (max-width: 980px){
    .cart-section__container{
        flex: 0 0 100%;
        width: 100%;
        overflow-y: visible;
        top: 0;
        padding: 1rem;
    }
}
.payment__container{
    position: relative;
    flex: 0 0 30%;
    width: 30%;
}
.dot__button{
    position: absolute;
}
.payment{
    height: 100vh;
    background-color: #493F3D;
    padding: 4rem;
    color: #ffffff;
    overflow-y: auto;
}
.payment__title{
    color: #F1C40F;
    font-size: 2rem;
    margin-bottom: 2rem;
    font-weight: 100;
}

.bank-account__list{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0;
}
.bank-account__item{
    color: #aaaaaa;
    transition: color 200ms;
}
.bank-account__item.active{
    color: #ffffff;
}
.bank-account__item:hover{
    cursor: pointer;
    color: #ffffff;
}
.bank-account__item svg{
    width: 50px;
}

.row{
    display: flex;
    align-items: center;
}
.two-col__item{
    margin: 0 0.5rem;
}
.little-inputs input{
    font-size: 1.3rem !important;
    text-align: center;
}
.form-control{
    display: flex;
    flex-direction: column;
}
.form-control input{
    border-bottom: 1px solid #ffffff;
    color: #ffffff;
    font-size: 1.3rem;
    letter-spacing: 0.15rem;
}
.date__input{
    display: flex;
    align-items: center;
}
.date__input span{
    margin: 1rem 0.5rem 0;
}
label{
    padding: 1.5rem 0 0.5rem;
}

.payment__button{
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    padding: 1.7rem 0;
    background-color: #F1C40F;
    color: #353535;
    font-size: 1.2rem;
    font-weight: 600;
}

@media (max-width: 980px){
    .payment__container{
        flex: 0 0 100%;
        width: 100%;
    }
    .dot__button{
        display: none;
    }
    .payment{
        padding: 5rem 5rem 10rem 5rem;
        height: auto;
    }
}
@media (max-width: 765px){
    .payment{
        padding: 2rem 1.5rem 10rem;
    }
    .bank-account__item svg{
        width: 70px;
    }
}
@media (max-width: 330px){
    .payment{
        padding: 2rem 1.5rem 10rem 1.5rem;
    }
}

