.cart-section__container{
    flex: 0 0 70%;
    width: 70%;
    overflow-y: scroll;
    padding: 2rem 3rem;
}

@media (max-width: 980px){
    .cart-section__container{
        flex: 0 0 100%;
        width: 100%;
        overflow-y: visible;
        top: 0;
        padding: 1rem;
    }
}