.header{
    display: flex;
    align-items: center;
}
.app-logo img{
    width: 100%;
}
.vertical-seperator{
    width: 1px;
    height: 30px;
    background-color: #aaaaaa;
    margin: 0 1.5rem 0 1rem;
}
.app-header{
    font-weight: 200;
    margin: 0;
    color: #454545;
    font-size: 1.5rem;
}

@media (max-width: 765px){
    .app-header,
    .vertical-seperator{
        display: none;
    }
}
